















import lausirCodeDialog from '../../components/lausir-codedialog/lausir-codedialog.vue';

export default {
	components: {
		lausirCodeDialog,
	},
	data() {
		return {
			timer: 60,
			show: false,
			followed:true,
			title: '获取验证码',
			showCodeDialog: false,
			mobile: '',
			verifyCode: ''
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '注册'
		});
	},
	methods: {
		getVerifyCode: function() {
			this.showCodeDialog = true;
			var _this = this;
			let timer1 = setInterval(() => {
						_this.show = true;
						_this.followed = false;
						_this.timer--;
						_this.title = _this.timer + 's后再试';
						if (_this.timer == 0) {
							clearInterval(timer1);
							_this.timer = 60;
							_this.show = false;
							_this.title = '发送验证码';
							return;
						}
					},1000); 
			uni.request({
				url: this.apiServer + '/user/verify',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: _this.mobile
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '验证码已发送'
						});
						_this.disabled = true;
						console.log(_this.disabled);
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		},
		checkCode: function() {
			var _this = this;
			console.log(_this.verifyCode);
			uni.request({
				url: this.apiServer + '/user/check',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: _this.mobile,
					verifyCode: _this.verifyCode
				},
				success: res => {
					console.log(res.data);
					if (res.data.code === 0) {
						uni.navigateTo({
							url: '../password/password?mobile=' + _this.mobile
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
						this.showCodeDialog = true;
					}
				}
			});
		},

		change: function(res) {
			if (res.type == 1) {
				this.verifyCode = res.code;
				this.showCodeDialog = false;
			} else if (res.type == -1) {
				this.verifyCode = "请输入验证码";
				this.showCodeDialog = false;
			} else {
				setTimeout(function() {
					res.resendCall()
				}, 3000)
			}


		}
	}

};
